
import './App.css';
import React, { Component } from 'react';
import ReactSwipe from 'react-swipe';
import Test1 from './component/Test1.js'
import Test2 from './component/Test2.js'
import Test3 from './component/Test3.js'
import Bouzin from './component/Bouzin';




let reactSwipeEl;
class App extends Component {
  constructor() {
    super();

    this.state = {
      active: 0

    };
  }

  update = () => {
    console.log("Updated!",this.state.active);


    this.setState({
      active: reactSwipeEl.getPos()
    }
    )


  }





  render() {

    return (
      <div className="App">
        <div>
          <ReactSwipe
            className="carousel"
            swipeOptions={{
              continuous: false,
              callback: this.update,
            }}
            ref={el => (reactSwipeEl = el)}

          >
            <div > <Test1 /></div>
            <div ><Test2 /></div>
            <div ><Test3 /></div>

          </ReactSwipe>

          <button onClick={() => reactSwipeEl.next()}>Next</button>
          <button onClick={() => reactSwipeEl.prev()}>Previous</button>
          <button onClick={this.update}>update</button>

        </div>
        <Bouzin active={this.state.active}  />

      </div>
    )


  }
















}



export default App;

